<template>
  <div class="info">
    <div class="bread">
      <a href="javascript:void(0)" style="font-size: 0.14rem;color: #b4b4b5" @click="goBack">我的模板</a>
      <span style="color: #b4b4b5">/</span>
      <span>模板详情</span>
      <span class="goBack" @click="goBack">返回</span>
    </div>
    <div class="content">
        <section style="width: 3.2rem;  margin-right: 0.2rem;">
          <div class="left">
            <div class="left_h">
              <img :src="baseInfo.pic" alt="" style="width: 50px;max-height: 100%;border-radius: 50%">
            </div>
            <div class="left_c">
              <header>
                <p>{{template.title}}</p>
                <time class="font" style="width: auto">{{time}}</time>
              </header>
              <p v-for="item in data">
                <span class="font">{{item}}</span>
                <strong>咖啡</strong>
              </p>
            </div>
            <div class="left_f">
              <p class="font">查看详情</p>
              <Icon type="chevron-right" color="#b4b4b5"></Icon>
            </div>
          </div>
          <p style="text-align: center;margin: 0.2rem 0;width: 100%" class="font">内容实例</p>
        </section>
        <section class="right">
          <p class="line">
            <span class="font">模板ID</span>
            <strong>{{template.template_id}}</strong>
          </p>
          <p class="line">
            <span class="font">标题</span>
            <strong>{{template.title}}</strong>
          </p>
          <div style="display: flex" class="line">
            <span class="font">关键词</span>
            <div>
                 <p v-for="item in data">
                   <span class="font">{{item}}</span>
                    <strong>keyword1.DATA</strong>
                  </p>
            </div>
          </div>
        </section>
    </div>
  </div>
</template>
<script>
export default{
    data(){
        return {
          time:'',
          template:{},
          baseInfo:{},
          data:[]
        }
    },
  methods:{
    goBack(){
        this.$emit("flags","1")
    }
  },
  watch:{

  },
  mounted(){
    this.baseInfo=JSON.parse(sessionStorage.getItem("baseInfo"));
    this.template=JSON.parse(sessionStorage.getItem('templateInfo'));
    this.time=new Date(this.template.time*1000).toLocaleString();
    this.data=this.template.keyword_name_list.split(',');
  }
}
</script>
<style scoped>
  strong{  font-weight: 400;  }
  .font{  color:#b4b4b5 ;margin-right: 0.2rem;display: inline-block;width: 1rem}
  .line{margin: 0.15rem 0}
  .content{  padding: 0.2rem; display: flex;margin-top: 0.2rem; }
  .left{
    width: 3.2rem;
    /*padding: 0.1rem;*/
    border: 1px solid #f2f2f2;
  }
  .left_h{
    padding: 0.15rem 0.1rem;
    border-bottom: 1px solid #f2f2f2;
  }
  .left_c{
    padding: 0.1rem;
    min-height: 2.3rem;
  }
  .left_c>header{
    margin-bottom: 0.2rem;
  }
  .left_f{
    padding: 0.1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .goBack{
    position: relative;
    left: 12rem;
    cursor: pointer;
  }
</style>



